.monthly{
    background: $cyan;
    display: block;
    padding-left: 20px;
}

.monthly__title{
    color: $light-gray;
    margin-top: 30px;
}

.monthly__paragraph-currency{
    color: $light-gray;
}

.monthly__currency{
    display: inline-block;
    color: $light-gray;
    font-size: 35px;
    font-weight: 700;
}

.monthly__paragraph{
    color: $light-gray;
}

.signup{
    display: block;
    color: #fff;
    background-color: $bright-yellow;
    border: none;
    width: 90%;
    padding: 20px;
    margin-bottom: 40px;
    font-size: 20px;
    font-weight: 700;
    border-radius: 10px;

    &:hover{
        background-color:#6FC66C;
        transform: scale(.9);
        cursor: pointer;
    }
}
