@import url('https://fonts.googleapis.com/css2?family=Karla:wght@400;700&display=swap');
@import 'variables';
@import 'join-section';
@import 'monthly-section';
@import 'why-section';

html{
    box-sizing: border-box;
}

*, *::before, *::after{
    box-sizing: inherit;
}

body{
    background: $light-gray;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    font-family: $font-family;
    font-size: $font-size;
}

.container{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 1fr);
    box-shadow: -2px 4px 5px 4px rgba(0,0,0,0.47);
}
